.footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-top: auto;
  background-color: rgba(52, 145, 88, 0.135);
  font-weight: 700;
  text-align: center;
  font-size: 1rem;
  padding: 2rem;
  border-radius: 7px;
}

.nav ul {
  list-style: none;
  display: flex;
  align-content: center;
  border-radius: 3px;
  margin: 1.3rem;
}

.nav li {
  margin: 0 10px;
}

.logo {
  border-radius: 7px;
  width: 4rem;
  height: auto;
  object-fit: cover;
  margin-left: 1rem;
}

nav a:link,
nav a:visited {
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;
  font-weight: 400;
  padding: 11px 11px;
  border-radius: 3px;
}

.nav a:global(.active) {
  background-color: rgba(52, 145, 87, 0.345);
}

.nav a:active,
.nav a:hover {
  background-color: rgba(52, 145, 87, 0.345);
}

.cartItemIndicator {
  position: absolute;
  top: 0.6rem;
  right: 2rem;
  color: rgba(52, 145, 87, 0.345);
  text-align: center;
  font-weight: 700;
}

@media screen and (max-width: 900px) {
  .nav {
    font-size: 1.2rem;
    margin: 20px;
  }

  .logo {
    width: 5rem;
    margin: 20px;
  }

  .footer {
    flex-direction: column;
  }
}
